import React from 'react';
import { myUrl } from './Url';

export const TweetCard = (props) => {
      const dataset = props.dataset
      let media;
      if (dataset.has_media) {
        if (dataset.media_type === "photo") {
          media = <img src={dataset.media_image_url} className="my-1 my-md-2 twitmedia" alt={dataset.media_image_url}/>
        } else {
          media = <video className="my-1 my-md-2 twitmedia" alt={dataset.media_image_url} playsInline autoPlay loop muted>
            <source src={dataset.media_image_url} type="video/mp4" />
          </video>
        }
      } else {
        media = <div></div>
      }

      return (
        <div className="card w-100 pb-0 mb-2 tweetcard restext_2" style={{display:'none'}} time_stamp={dataset.time_stamp} id={"time-"+ dataset.time_stamp}>
            <div className="card-body px-2 pt-2 pb-0">
              <p className="h6 card-title d-inline-flex mb-1 mr-1 restext_1">{dataset.tweeter_name}</p>
              <p className="h6 card-subtitle d-inline-flex text-muted restext_1">@{dataset.screen_name}</p>
              <p className="card-text mb-1">{dataset.tweet_text}</p>
              {media}
            </div>
            <div className="text-muted py-0 pl-2 d-flex flex-row align-items-center">
              <img className="iconer" src={myUrl+"/loop-square.svg"} alt="loop-square"/>
              <div className="m-1 mr-3">{dataset.retweets}</div>
              <img className="iconer" src={myUrl+"/heart.svg"} alt="heart"/>
              <div className="m-1 mr-3">{dataset.favorites}</div>
              <img className="iconer" src={myUrl+"/clock.svg"} alt="clock"/>
              <div className="m-1">{dataset.interval}</div>
            </div>
          </div>
        );
  }
