import React, { Component } from 'react';
import $ from 'jquery';
import { myUrl } from './Url';

export class Controls extends Component {
  constructor(props) {
    super(props);
    this.myRef = React.createRef();
    this.incrementTime = this.incrementTime.bind(this);
    this.toggleTime = this.toggleTime.bind(this);
    this.skipForward = this.skipForward.bind(this);
    this.skipBack = this.skipBack.bind(this);
    this.toggleTweet = this.toggleTweet.bind(this);
    this.state = { timer_on: 0 };
    this.ffStart = this.ffStart.bind(this);
    this.ffStop = this.ffStop.bind(this);
    this.rwStart = this.rwStart.bind(this);
    this.rwStop = this.rwStop.bind(this);
  }

  // adds leading zeros for time formatting
  zeroPad(num, places) {
    var zero = places - num.toString().length + 1;
    return Array(+(zero > 0 && zero)).join("0") + num;
  }

  // formats milliseconds to time
  formatTime(t) {
    var sec_ms = 1000;
    var min_ms = 1000*60;
    var hour_ms = 1000*60*60;
    var day_ms = 1000*60*60*24;
    // var days = Math.floor(t/day_ms);
    var hours = Math.floor((t % day_ms)/hour_ms);
    var minutes = Math.floor((t % hour_ms)/min_ms);
    var seconds =  Math.floor((t % min_ms)/sec_ms);

    return hours + ':' + this.zeroPad(minutes,2) + ':' + this.zeroPad(seconds,2)
  }

  // functions for updating the clock values with the slider value and toggling tweets
  updateAll = () => {
    var slider = this.myRef.current
    $("#clock1").html(this.formatTime(slider.value));
    $("#clock2").html("-" + this.formatTime((45)*1000*60 - slider.value ));
    this.toggleTweet();
  }

  // toggles the timer on and off
  timer;
  toggleTime() {
    var timer_on = this.state.timer_on;
    if (timer_on === 1) {
      this.setState({timer_on:0});
      clearInterval(this.timer);
      $("#play_pause").html("Play");
    } else {
      this.setState({timer_on:1});
      this.timer = setInterval(this.incrementTime, 1000);
      $("#play_pause").html("Pause");
    };
  }

  // increments slider value by one second each seconds
  incrementTime() {
    var slider = this.myRef.current
    if (Number(slider.value) < Number(slider.getAttribute('max'))) {
      var x = Number(slider.value) + 1000;
      slider.value = x;
      slider.setAttribute('value',x);
      this.updateAll();
    }
  }

  // increments slider value by 15 seconds
  skipForward() {
    var slider = this.myRef.current
    var x = Math.min(Number(slider.value) + 15000,(30+15)*60*1000);
    slider.value = x;
    slider.setAttribute('value',x);
    this.updateAll();
  }

  // decreases slider value by 15 seconds
  skipBack() {
    var slider = this.myRef.current
    var x = Math.max(Number(slider.value) - 15000,0);
    slider.value = x;
    slider.setAttribute('value',x);
    this.updateAll();
  }

  // shows or hides the tweet depending on the timestamp
  toggleTweet() {
    var slider = this.myRef.current
    var tweetc = $(".tweetcard");
    tweetc.filter(function(index) {
      return Number($(this).attr("time_stamp")) <= Number(slider.value);
    }).slideDown();
    tweetc.filter(function(index) {
      return Number($(this).attr("time_stamp")) > Number(slider.value);
    }).hide();
  }

  // continuously increases slider by 15 seconds whilst button is held down
  ff_toggle
  ff_toggle2

  ffStart() {
    this.ff_toggle = setTimeout(() => {this.ff_toggle2 = setInterval(this.skipForward, 50)}, 500);
  }

  ffStop() {
      clearInterval(this.ff_toggle);
      clearTimeout(this.ff_toggle2);
  }

  // continuously decreases slider by 15 seconds whilst button is held down
  rw_toggle
  rw_toggle2

  rwStart() {
    this.rw_toggle = setTimeout(() => {this.rw_toggle2 = setInterval(this.skipBack, 50)}, 500);
  }

  rwStop() {
      clearInterval(this.rw_toggle);
      clearTimeout(this.rw_toggle2);
  }

  timer;
  componentDidMount() {
      this.timer = setTimeout(this.updateAll,3000);
  }
  componentWillUnmount() {
      clearTimeout(this.timer);
  }

  render() {
    return (
      <div>
        {/* TIME SLIDER */}
        <div className="container">
          <div className="row mx-0 mb-1 py-2 justify-content-between">
            <div className="col pr-2 pl-0 clocks restext_1 m-auto flex-grow-0" id="clock1">0:00:00</div>
            <div className="col p-0 my-auto flex-grow-1">
              <input type="range" className="slider my-auto" id="timeslider" min="0" max={45*60*1000} ref={this.myRef} onInput={this.updateAll} defaultValue={0}/>
            </div>
            <div className="col pr-0 pl-2 clocks restext_1 m-auto flex-grow-0 text-right" id="clock2">-0:45:00</div>
          </div>
        </div>

        {/* CONTROL BUTTONS */}
        <div className="container mb-2">
          <div className="m-auto row">
            <button type="button" className="mybutton blue1 col-auto mr-2"
              id="control-back" onClick={this.skipBack} onMouseDown={this.rwStart}
              onMouseUp={this.rwStop} onMouseOut={this.rwStop}>
              <img className="iconer2" src={myUrl+"/media-skip-backward.svg"} alt="skip-backward"/>
            </button>
            <button type="button" className="mybutton blue1 col" id="play_pause"
              onClick={this.toggleTime}>
              Play
            </button>
            <button type="button" className="mybutton blue1 col-auto ml-2"
              id="control-forward" onClick={this.skipForward} onMouseDown={this.ffStart}
              onMouseUp={this.ffStop} onMouseOut={this.ffStop}>
              <img className="iconer2" src={myUrl+"/media-skip-forward.svg"} alt="skip-forward"/>
            </button>
          </div>
        </div>
      </div>
    );
  }
}
