import React from 'react';
import $ from 'jquery';
import { ListGroup } from 'reactstrap';
import { Link } from 'react-router-dom';

export const EpisodeList = (props) => {
      function toggleList(e) {
        $(e.currentTarget).siblings().slideToggle(600);
      }

      const episodes = props.episodeList.map((ep,i) =>
        <Link
          className="list-group-item list-group-item-action restext_1 py-2 px-3"
          key={i}
          to={{
            pathname: '/react/episodepage/'+ep.id,
            state: {
              episode: {
                "season_num": ep.season_num,
                "episode_num": ep.episode_num,
                "episode_name": ep.episode_name,
                "episode_len": ep.episode_len,
              },
            },
          }}
        >
          {ep.episode_num}: {ep.episode_name}
        </Link>
      )
      return (
          <ListGroup>
            <button className="ses mybutton blue1 btn-block my-1" onClick={toggleList}>{props.buttonName}</button>
            <div style={{ display:'none'}}>
              {episodes}
            </div>
          </ListGroup>
      );
  }
