import React, { Component } from 'react';
import NavBar from './Components/NavBar';
import {ProgramsPage, ProgramsCrumb} from './Components/ProgramsPage';
import {SeasonList, SeasonListCrumb} from './Components/SeasonList';
import {EpisodePage, EpisodeCrumb} from './Components/EpisodePage';
import { Home } from './Components/Home';
import { About, AboutCrumb } from './Components/About';
import { Route, Switch } from 'react-router-dom';


export class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      program: {
          "id": 1,
          "name": "Insecure",
          "image_path": "insecure.1200.675.jpeg"
      },
      episode: {
        "season_num": 4,
        "episode_num": 4,
        "episode_name": "Test-Like",
        "episode_len": "00:30:00",
      },
      sdelta: 15,
    };
    this.updateApp = this.updateApp.bind(this);
  }

  updateApp(obj) {this.setState(obj)}

  render() {
    return (
      <div>
        <NavBar/>
          <Switch>
            <Route exact path='/react/programs' component={ProgramsCrumb} />
            <Route exact path='/react/about' component={AboutCrumb} />
            <Route exact path='/react/programs/:number' render={(props) =>
                <SeasonListCrumb {...props} current={this.state} />} />
            <Route exact path='/react/episodepage/:number' render={(props) =>
                <EpisodeCrumb {...props} current={this.state} />} />
          </Switch>
          <Switch>
            <Route exact path='/react' component={Home} />
            <Route exact path='/react/programs' render={(props) =>
                <ProgramsPage {...props} current={this.state} updateApp={(info) => this.updateApp(info)} />} />
            <Route exact path='/react/about' component={About} />
            <Route exact path='/react/programs/:number' render={(props) =>
                <SeasonList {...props} current={this.state} updateApp={(info) => this.updateApp(info)} />} />
            <Route exact path='/react/episodepage/:number' render={(props) =>
                <EpisodePage {...props} current={this.state} updateApp={(info) => this.updateApp(info)} />} />
          </Switch>
      </div>
    );
  }
}
