import React, { Component } from 'react';
import { Carousel } from './Carousel';
import { ProgramList } from './ProgramList';
import { hostUrl } from './Url';
import { Breadcrumbs } from './Breadcrumbs';

export const ProgramsCrumb = () => {
  return (
    <Breadcrumbs>
      <li className="breadcrumb-item active">Programs</li>
    </Breadcrumbs>
  );
}

export class ProgramsPage extends Component {
  constructor(props) {
    super(props);
    this.state = { dataset: [] };
  }

  async componentDidMount() {
    try {
      const res = await fetch(hostUrl+'api/programlist/');
      const programlist = await res.json();
      this.setState({
        dataset: programlist
      });
    } catch (e) {
      console.log(e);
    }
  }

  render() {
    return (
      <div className="container">
        <Carousel programlist={this.state.dataset}/>
        <hr />
        <ProgramList programlist={this.state.dataset}/>
      </div>
    );

  }
}
