import React from 'react';
import {  Navbar,  Nav,  NavItem } from 'reactstrap';
import { Link } from 'react-router-dom';

export default class NavBar extends React.Component {
  render() {
    return (
      <div>
        <Navbar color="dark" dark expand="xs">
          <Link to={'/react'}><span className="navbar-brand mr-2">RewyndApp</span></Link>
            <Nav className="mr-auto" navbar>
              <NavItem><Link to={'/react/programs'} className="nav-link text-secondary px-1">Programs</Link></NavItem>
              <NavItem><Link to={'/react/about'} className="nav-link text-secondary px-1">About</Link></NavItem>
            </Nav>
        </Navbar>
      </div>
    );
  }
}
