import React, { Component } from 'react';
import {TweetCard} from './TweetCard';
import {Controls} from './Controls';
import { hostUrl } from './Url';
import { Breadcrumbs } from './Breadcrumbs';
import { Link } from 'react-router-dom';

const IntroText = (props) => {
    return (
      <span>
        Ready? Hit play to start!<br /><br />
        {props.episode_info.episode_string} is {props.episode_info.episode_len} mins long and
      has {props.episode_info.sdelta} mins of tweets after the show.
      The first tweet is {props.tweet_info[0] ? "at "+props.tweet_info[0].interval : "soon"}.
      </span>
    );
}

export const EpisodeCrumb = (props) => {
  const b = props.current
  const program = b.program.name;
  const program_id = b.program.id;
  const c = b.episode;
  const episode_name = "S"+c.season_num+":E"+c.episode_num+" "+c.episode_name;

    return (
      <Breadcrumbs>
        <li className="breadcrumb-item"><Link to={'/react/programs'}><span>Programs</span></Link></li>
        <li className="breadcrumb-item"><Link to={'/react/programs/'+program_id}>{program}</Link></li>
        <li className="breadcrumb-item active">{episode_name}</li>
      </Breadcrumbs>
  );
}


export class EpisodePage extends Component {
  constructor(props) {
    super(props);
    this.state = { dataset: false };
  }

  componentWillMount() {
    this.props.updateApp(this.props.location.state);
  }

  episode = parseInt(this.props.match.params.number, 10)
  async componentDidMount() {
    try {
      const res = await fetch(hostUrl+'api/episodetweets/'+this.episode);
      const dataset = await res.json();
      this.setState({
        dataset: dataset
      });
    } catch (e) {
      console.log(e);
    }

  }

  parseInterval(str) {
    var o = parseInt(str.slice(0,2))*60*60*1000 +
            parseInt(str.slice(3,5))*60*1000 +
            parseInt(str.slice(6,8))*1000;
    return o;
  }

  render() {
    const c = this.props.location.state ? this.props.location.state.episode : this.props.current.episode;
    const episode_string = "S"+c.season_num+":E"+c.episode_num+" "+c.episode_name;

    const episode_info = {
      episode_string:episode_string,
      episode_len:this.parseInterval(c.episode_len)/60/1000,
      sdelta:15,
    };

    let tweet_list = false;
    let tweets = this.state.dataset;
    if (tweets) {
      tweet_list = tweets.map((tweet,i) =>  {
        var t = {
          time_stamp:this.parseInterval(tweet.interval)+1,
          tweeter_name:tweet.tweeter.name,
          screen_name:tweet.tweeter.screen_name,
          tweet_text:tweet.text,
          retweets:tweet.retweets,
          favorites:tweet.favorites,
          interval:tweet.interval,
          has_media:tweet.has_media,
          media_type:tweet.media_type,
          media_image_url:tweet.media_image_url,
        };
        return t;
      });
    };

    const intro_tweet = {
      time_stamp:-1,
      tweeter_name:"Insecure Fan",
      screen_name:"rewynd_app",
      tweet_text:<IntroText episode_info={episode_info} tweet_info={tweet_list} />,
      retweets:"575",
      favorites:"874",
      interval:"0:00:00"
    };

    return (
      <div>
      <div className="my-2">
        <h1 className="text-center mt-2 mb-0 restext_title">{episode_string}</h1>
        <div className="container p-0" style={{maxWidth:'600px'}}>
          <Controls />
          {/* TWEET CONTENT */}
          <div className="container">
            <div className="d-flex flex-column-reverse align-items-center">
              <TweetCard dataset={intro_tweet} />
                {tweet_list && tweet_list.map((datas,i) => <TweetCard key={i} dataset={datas} />)}
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
