import React, { Component } from 'react';
import { EpisodeList } from './EpisodeList';
import { hostUrl } from './Url';
import { Breadcrumbs } from './Breadcrumbs';
import { Link } from 'react-router-dom';

export const SeasonListCrumb = (props) => {
  const program = props.current.program.name;

  return (
    <Breadcrumbs>
      <li className="breadcrumb-item"><Link to={'/react/programs'}><span>Programs</span></Link></li>
      <li className="breadcrumb-item active">{program}</li>
    </Breadcrumbs>
  );
}

export class SeasonList extends Component {
  constructor(props) {
    super(props);
    this.state = { dataset: [] };
  }

  season_sort(arr) {
    var a = {};
    for(var i = 0, l = arr.length; i < l; ++i){
        if(!a.hasOwnProperty(arr[i].season_num)) {
          a[arr[i].season_num] = [arr[i]];
        } else {
          a[arr[i].season_num].push(arr[i]);
        }
    }
    return a;
  }

  componentWillMount() {
    this.props.updateApp(this.props.location.state);
  }

  program = parseInt(this.props.match.params.number, 10)
  async componentDidMount() {
    try {
      const res = await fetch(hostUrl+'api/episodelist/'+this.program);
      const episodelist = await res.json();
      this.setState({
        dataset: episodelist
      });
    } catch (e) {
      console.log(e);
    }
  }

  render() {

    let seasons;
    if (this.state.dataset.length > 0) {
      var sorted_eps = this.season_sort(this.state.dataset)
      var sorted_ses = Object.keys(sorted_eps).sort()
      seasons = sorted_ses.map((item, i) =>
        <EpisodeList buttonName={'Season '+item} key={i} episodeList={sorted_eps[item]}/>
      );
    } else {
      seasons = <div className="text-center">More episodes coming soon!</div>
    }

    const program_name = this.props.location.state ?
      this.props.location.state.program.name :
      this.props.current.program.name

    return (
      <div>
        <h1 className="text-center mb-2" style={{fontSize: "30px"}}>{program_name}</h1>
        <div className="container w-75">
          {seasons}
        </div>
      </div>
    );
  }
}
